.concept{
  position: relative;
  &::after{
    margin-left: calc(50% - 50vw);
    margin-right: calc(50% - 50vw);
    padding-left: calc(50vw - 50%);
    padding-right: calc(50vw - 50%);
    position: absolute;
    display: block;
    content: "";
    z-index: -1;
    top: 400px;
    background: $c_key3;
    width: 100vw;
    height: 150px;
    top: 580px;
    @include large{
      height: 240px;
      top: 570px;
    }
  }
}
.concept__img{
  object-fit: cover;
  height: 350px;
  @include medium{
    height: 400px;
  }
}
