/* section
------------------------------------------------------ */
.section {
  padding-top: 60px;
  padding-bottom: 60px;
  @include large {
    padding-top: 80px;
    padding-bottom: 80px;
  }
}


/*セクション付属タイトル*/
.section__title {
  position: relative;
  text-align: center;
  font-size: 24px;
  margin: 0 auto;
  font-family: 'Poppins', sans-serif;
  color: $c_key1;
  padding-bottom: 50px;
  &::after{
    position: absolute;
    content: "";
    display: block;
    width: 40px;
    height: 1px;
    background: $c_key1;
    left: calc(50% - 20px);
    top: 60px;
    @include large{
      top: 80px;
    }
  }
  @include large {
    padding-bottom: 40px;
    font-size: 36px;
    & + * {
      margin-top: 20px;
    }
  }
}

/*セクション付属リード文*/
.section__lead {
  text-align: center;
  font-size: 14px;
  margin-bottom: 20px;
  & + * {
    margin-top: 10px;
  }
  @include large {
    font-size: 16px;
    margin-bottom: 40px;
  }
}
