/* gnav
------------------------------------------------------ */
.gnav {
  position: fixed;
  top: -100vh;
  left: 0;
  z-index: 1000;
  height: calc(100vh - 104px);
  width: 100vw;
  padding: 40px 25px;
  opacity: .9;
  background-color: #fff;
  transition: top 0.5s;
  font-family: "Poppins", sans-serif;
  text-align: center;
  font-size: 15px;
  @include large{
    font-size: 20px;
  }
}
.menu__item{
  margin: 0 auto;
  border-bottom: 1px solid #a0a0a0;
  width: 200px;
  padding: 20px 0;
  &:last-child{
    border-bottom: 0;
  }
  @include large{
    width: 300px;
  }
}
/*! purgecss start ignore */
.open {
  .gnav {
    top: 60px;
    @include large{
      top: 75px;
    }
  }
}
/*! purgecss end ignore */
