.mv {
  z-index: -1;
  height: 350px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: url(/assets/img/header@2x.png) center center no-repeat;
  background-size: cover;
  color: $c_white;
  @include medium {
    height: 620px;
  }
}
.mv__title {
  font-size: 90px;
  font-weight: normal;
  @include medium{
    font-size: 120px;
  }
}
.mv__title-lead{
  margin-top: -20px;
  font-weight: 600;
  position: relative;
  &::before{
    position: absolute;
    content: '';
    display: block;
    width: 12px;
    height: 2px;
    background: $c_white;
    top: 15px;
    left: -20px;
  }
  &::after{
    position: absolute;
    content: '';
    display: block;
    width: 12px;
    height: 2px;
    background: $c_white;
    top: 15px;
    right: -20px;
  }
}
