/* hamburger
------------------------------------------------------ */
/*! purgecss start ignore */
.open{
  .hamburger span:nth-child(2) {
    background: transparent;
  }

  .hamburger span:nth-child(3) {
    transform: translateY(-10px) rotate(315deg);
  }
  .hamburger span:nth-child(1) {
    transform: translateY(10px) rotate(-315deg);
  }
  .hamburger .spmenu {
    left: 0;
    opacity: 1;
  }
}

.hamburger {
  width: 30px;
  height: 30px;
  cursor: pointer;
  background: inherit;
  position: fixed;
  right: 20px;
  top: 15px;
  & .open {
    display: block;
  }
  @include large{
    top: 30px;
  }
}
.hamburger__line {
  width: 30px;
  height: 1px;
  background-color: $c_white;
  border-radius: 4px;
  position: absolute;
  left: 0;
  transition: 0.5s;
}
.hamburger span:nth-child(1) {
  top: 4px;
}
.hamburger span:nth-child(2) {
  top: 14px;
}
.hamburger span:nth-child(3) {
  bottom: 4px;
}

/*! purgecss end ignore */