/* header
------------------------------------------------------ */
/*! purgecss start ignore */
.header {
  color: $c_white;
  height: 64px;
  padding-left: 20px;
  padding-right: 20px;
  position: fixed;
  width: 100%;
  z-index: 100;
  @include large {
    height: 90px;
  }
  &.bgc {
    background-color: $c_white;
    color: $c_text;
    & .hamburger__line {
      background-color: $c_text;
    }
  }
}
.header__title{
  font-size: 24px;
  position: absolute;
  top: 10px;
  @include large{
    font-size: 36px;
    top: 20px;
  }
}
/*! purgecss end ignore */
