.florist__list{
  display: flex;
  overflow-x: scroll;
  margin-bottom: 20px;
  @include large{
    overflow-x: hidden;
  }
  &::-webkit-scrollbar{
    border-radius: 10px;
    height: 5px;
    background: #ffffff;
  }
  &::-webkit-scrollbar-thumb{
    border-radius: 10px;
    background: #bebebe;
  }
}
.florist__item{
  height: auto;
  width: 300px;
  margin-right: 20px;
  @include large{
    margin-right: 30px;
    &:last-child{
      margin-right: 0;
    }
  }
}

.florist{
  position: relative;
  &::before{
    margin-left: calc(50% - 50vw);
    margin-right: calc(50% - 50vw);
    padding-left: calc(50vw - 50%);
    padding-right: calc(50vw - 50%);
    position: absolute;
    display: block;
    content: "";
    z-index: -1;
    top: 400px;
    background: $c_key3;
    width: 100vw;
    height: 100%;
    top: 0px;
    @include large{
      height: 374px;
    }
  }
}

.card__img{
  width: 300px;
  object-fit: cover;
}
.card__text{
  padding-bottom: 20px;
}